.App{
  width: 100%;
  height: 100vh;
  font-size: 18px;
}

header{
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* ↓背景 */
  background-color: chartreuse;  
  color: white;
  /* position: fixed; */
  width: 100%;
  z-index: 10;
}

header .logo h3 {
  font-size: 30px;
}

/* 全navだね */
nav ul li {
  list-style: none;
  display: inline-block;
  margin-right: 30px;
}


nav ul li a {
  text-decoration: none;
  color: white;
  transition: all 0.2s;
}

/* ホバーしたら */
nav ul li a:hover {
  color: black;
}